
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-sidebar {
  position: absolute;
  top: 0;
  left: 0;
  width: 200px;
  height: 100%;
  background: #282c34;
  color: white;
  padding: 20px;
}

.App-sidebar-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.App-sidebar-info-avatar {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background: #282c34;
  margin-bottom: 20px;
}

.App-avatar{
  width: 100px;
  height: 100px;
  border-radius: 20%;
  background: gray;
  margin-bottom: 20px;
}

.App-sidebar-info-name {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 10px;
}

.App-sidebar-info-title {
  font-size: 14px;
  margin-bottom: 10px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
